    methods: {
      copy() {
        var that = this;
        console.log("clk");
        var clipboard = new this.clipboard(".copy-text");
        clipboard.on("success", function (e) {
          e.clearSelection(); //ÈÄâ‰∏≠Êó∂‰∏çÊòæÁ§∫ÈÄâ‰∏≠ÁöÑËÉåÊôØËâ≤
          that.$message({
            message: "Â§çÂà∂ÊàêÂäü",
            type: "success",
          });
        });
        clipboard.on("error", function () {
          that.$message({
            message: "Â§çÂà∂Â§±Ë¥•",
